<template>
    <div class="common-wrap">
        <div class="common-left" :style="{width: (100 - otherVariable.contentWidth) + '%'}" v-show="!otherVariable.isSpread">
            <div class="not-allowed-wrap"  v-if="isShowNotAllowed"></div>
            <!---->
            <div class="common-btn-wrap">
                <!--<el-button class="btn-cyan add-btn" @click="handleMulImport" :disabled="otherVariable.isSaving"></el-button>-->
                <el-button @click="handleRefresh"></el-button>
                <el-checkbox style="margin-left: 10px;" v-model="isImport" @change="changeImport"></el-checkbox>
            </div>
            <!---->
            <CommonSearch
                :isShowStatus="isShowStatus"
                :propData="searchProp"
                :searchForm="searchForm"
                @selectChange="selectChange"
                @getData="getData"
            >
                <template slot="search-other-btn">
                    <el-button @click="handleReset"></el-button>
                </template>
            </CommonSearch>
            <!---->
            <CommonTable
                v-if="isResize"
                ref="commontable"
                :mainHeight="mainHeight"
                :isShowStatus="isShowStatus"
                :propData="tableProp"
                :otherVariable="otherVariable"
                :tableData="tableData"
                @getInitPage="getInitPage"
                @handleDetail="handleDetail"
                @handleSelectionChange="handleSelectionChange"
            >
            </CommonTable>
            <!---->
            <CommonPagination
                :pageObj="pageObj"
                @getData="getData"
            >
            </CommonPagination>
        </div>
        <div class="common-right" :style="{width: otherVariable.contentWidth + '%'}">
            <!--/-->
            <CommonDetailX
                :otherVariable="otherVariable"
                :propData="formProp"
                :detailForm="detailForm"
                @toggleSpread="toggleSpread"
            >
                <!---->
                <template slot="header">
                    <div class="header-form" ref="headForm">
                        <template v-for="item in formProp">
                            <div v-if="item.type === 'outter'">
                                <span>{{ item.name ? (item.name + ':') : '' }}</span>
                                <span>{{ detailForm[item.prop] }}</span>
                            </div>
                        </template>
                    </div>
                </template>
                <!---->
                <template slot="main-btn-wrap">
                    <div class="xs-audit-icon" v-if="!isBanOperate" :class="otherVariable.isSaving ? 'banClick' : ''" @click="!otherVariable.isSaving && handleImport()"></div>
                    <div class="xs-unaudit-icon" v-if="false" @click="handleUnImport"></div>
                </template>
                <!---->
                <template slot="head-form-title">
                    <div class="head-form-title">
                        <span>&nbsp;{{ detailForm.clientName }}</span>
                        <span></span>
                    </div>
                </template>
                <!---->
                <template slot="head-form-content">
                    <div class="head-form-content">
                        <template v-for="item in formProp">
                            <div v-if="item.type === 'inner' && !item.isLong">
                                <span>{{ item.name }}</span>
                                <span>{{ detailForm[item.prop] }}</span>
                            </div>
                        </template>
                    </div>
                    <div class="project-wrap">
                        <div></div>
                        <div class="scrollBar" :title="detailForm.projectName">
                            &nbsp;{{ detailForm.projectName }}{{ detailForm.projectCode?("("+ detailForm.projectCode + ")"):"" }}
                        </div>
                    </div>
                    <div class="remark-wrap scrollBar">
                        <div></div>
                        <div class="headspan" :title="detailForm.remark">{{ detailForm.remark }}</div>
                    </div>
                </template>
                <!---->
                <template slot="common-content">
                    <!--tab-->
                    <el-tabs v-model="activeName" class="common-content-tab order_style">
                        <el-tab-pane label="" name="first">
                            <CommonTable
                                v-if="isResize"
                                v-loading="loadingI"
                                ref="childTable"
                                :isSum="true"
                                :sumParam = {digit:8,theoryWght:11}
                                :isCenter="true"
                                :isMulTable="true"
                                :isNotMulSelect="false"
                                :otherVariable="otherVariable"
                                :formTableData="tabDataI"
                                :formPropTable="tabPropI"
                                :tableHeight="tableHeight"
                            >
                            </CommonTable>
                        </el-tab-pane>
                    </el-tabs>
                </template>
            </CommonDetailX>
        </div>
    </div>
</template>

<script>
    import CommonMixins from '$src/components/CommonMixins'
    import CommonSearch from '$src/components/CommonSearch'
    import CommonTable from '$src/components/CommonTable'
    import CommonPagination from '$src/components/CommonPagination'
    import CommonDetailX from '$src/components/CommonDetailX'
    export default {
        mixins: [CommonMixins],
        components: {
            CommonSearch,
            CommonTable,
            CommonPagination,
            CommonDetailX
        },
        data() {
            return {
                tableHeight: this.$store.state.screenHei*0.60,
                mainHeight: 716,
                isBanOperate: true,
                isShowNotAllowed: false,
                isShowStatus: {
                    isMore: false,
                    isRead: true
                },
                isImport: true,
                isResize: true,
                loadingI:false,
                otherVariable: {
                    isSpread: false,
                    contentWidth: 70,
                    popupTitle: '',
                    isMulSelect: true,
                    isInlineForm: true,
                    isChildEdit: false,
                    isHeadComplex: true,
                    isFormComplex: true,
                    isReadTable: true,
                    isSaving: false,
                },
                tempWidth: null,
                tableData: [],

                //
                searchProp: [
                    {
                        name:'',
                        prop:'refDocCode',
                        type:'normal',
                        isMainSearch:true,
                        isMain:false,
                        placeholder:''
                    },
                    {
                        name:'',
                        prop:'destClientCode',
                        type:'normal',
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'destClientName',
                        type:'normal',
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'tempStatus',
                        type:'selectChange',
                        selectList:[{value:1,label:''},{value:2,label:''},{value:3,label:''}],
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'orderDocDateStart',
                        type:'date',
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'orderDocDateEnd',
                        type:'date',
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'companyName',
                        type:'normal',
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'supplyCompanyName',
                        type:'normal',
                        isMainSearch:false,
                        isMain:false
                    },
                ],
                //
                formProp: [
                    {name:'',prop:'refDocCode',type:'outter'},
                    {name:'',prop:'docDate',type:'outter'},
                    {name:'',prop:'deliveryDate',type:'outter'},
                    {name:'',prop:'docStatusName',type:'outter'},
                    {name:'',prop:'destClientCode',type:'inner'},
                    {name:'',prop:'destClientCode2',type:'inner'},
                    {name:'',prop:'docType',type:'inner'},
                    {name:'',prop:'padPastingMethodName',type:'inner'},
                    {name:'',prop:'deliveryLevelName',type:'inner'},
                    {name:'',prop:'supplyCompanyName',type:'inner'},
                    {name:'',prop:'followName',type:'inner'},
                    {name:'',prop:'packMethodName',type:'inner'},
                    {name:'',prop:'vatTypeName',type:'inner'},
                    {name:'',prop:'alPriceDate',type:'inner'},
                    {name:'',prop:'usrTxt1Name',type:'inner'},
                    {name:'',prop:'remark',type:'inner',isLong:true}
                ],
                //
                tableProp: [
                    {
                        name:'fid',
                        prop:'fid',
                        type:'normal',
                        isMain:true
                    },
                    {
                        name1:'/',
                        prop1:'refDocCode',
                        name2:'',
                        prop2:'docStatusName',
                        type:'mixin',
                        width: '100'
                    },
                    {
                        name1:'/',
                        prop1:'docType',
                        name2:'',
                        prop2:'docDate',
                        type:'mixin',
                        width: '120'
                    },
                    {
                        name1:'/',
                        prop1:'destClientCode',
                        name2:'',
                        prop2:'destClientCode2',
                        type:'mixin',
                        width: '100'
                    },
                    {
                        name1:'/',
                        prop1:'docCode',
                        name2:'',
                        prop2:'projectName',
                        type:'mixin',
                    }
                ],
                searchForm: {docStatus: 100},
                detailForm: {},
                copyForm: {},
                detailId: null,
                pageObj: {
                    commonPage: 1,
                    commonSize: 10,
                    commonSizes: [10, 20, 30, 40, 50, 100],
                    commonTotal: 0
                },
                //
                tabLeft: '200px',
                activeName: 'first',
                //
                tabPropI: [
                    {prop:'docItem',name:'',type:'normal',isShort: true,width:'50',align:'center'},
                    {prop:'itemNo', name:'',type:'normal',isShort: true,width:'130'},
                    {prop:'matCode', name:'',type:'normal',isShort: true,width:'130'},
                    {prop:'cx1Name', name:'',type:'normal',isShort: true,width:'130'},
                    {prop:'cx2Name',name:'',type:'normal'},
                    {prop:'cx4Name',name:'',type:'normal',rule:'limitLength',unit:' m',isShort: true,align:'right'},
                    {prop:'cx3Name',name:'',type:'normal',unit:'',isShort: true,align:'right'},
                    {prop:'',name:'',type:'normal',align:'right'},
                    {prop:'digit',name:'',type:'normal',align:'right'},
                    {prop:'meterWght',name:'',type:'normal',rule:'limitLength',unit:' kg',isShort: true,align:'right'},
                    {prop:'unitWght',name:'',type:'normal',rule:'limitLength',unit:' kg/pcs',isShort: true,align:'right'},
                    {prop:'theoryWght',name:'',type:'normal',rule:'limitLength',unit:' kg',isShort: true,align:'right'},
                    {prop:'usrTxt1Name',name:'',type:'normal'},
                    {
                        prop:'usrTxt2Name',name:'',type:'normal',
                        children: [
                            {prop:'banSprayType',name:''},
                            {prop:'usrTxt2Name',name:''}
                        ]
                    },
                    {
                        prop:'',name:'',type:'normal',
                        children: [
                            {prop:'isTear',name:'',type:'checkbox',width: 55, align:'center'},
                            {prop:'usrTxt3Name',name:'',type:'checkbox',width: 55, align:'center'}
                        ]
                    },
                    {prop:'productDescription',name:'',type:'normal'},
                    {prop:'remark',name:'',type:'normal',width:200}
                ],
                //
                tabDataI: [],
                copyDataI: [],
                //
                selectOrderList: []
            }
        },
        mounted() {
            this.getData();
            // /
            this.listenScroll();
            this.listenResize();
            this.changeSize();
            this.tempWidth = this.otherVariable.contentWidth;
        },
        methods: {
            //
            getData(isFirst = true) {
                if(this.searchForm.refDocCode) {
                    this.searchForm = {refDocCode:this.searchForm.refDocCode}
                }else {
                    this.searchForm.refDocCode = null;
                }
                this.isBanOperate = true;
                this.tableData = [];
                this.fetch(`/oms/sls/SalesOrderMaster/getClientOrderMasterForSignAudit?pageNo=${this.pageObj.commonPage}&pageSize=${this.pageObj.commonSize}`,this.searchForm)
                    .then(result => {
                        if(result.data && result.data.list) {
                            this.tableData = result.data.list;
                            this.pageObj.commonTotal = result.data.total;
                            if(isFirst) {
                                this.$nextTick(() => {
                                    this.$refs.commontable.renderRow(this.tableData[0].fid);
                                })
                            }else {
                                this.$nextTick(() => {
                                    this.$refs.commontable.renderRow(this.detailId);
                                })
                            }
                        }else {
                            this.pageObj.commonTotal = 0;
                        }
                    })
            },
            selectChange(val) {
                if(val === 1) {
                    //
                    this.searchForm.docStatus = null;
                    delete this.searchForm.maxStatus;
                    delete this.searchForm.maxStatusOperator;
                    delete this.searchForm.minStatus;
                    delete this.searchForm.minStatusOperator;
                }else if(val === 2) {
                    //
                    this.searchForm.docStatus = 100;
                    delete this.searchForm.maxStatus;
                    delete this.searchForm.maxStatusOperator;
                    delete this.searchForm.minStatus;
                    delete this.searchForm.minStatusOperator;
                }else if(val === 3) {
                    //
                    this.searchForm.minStatus = 100;
                    this.searchForm.minStatusOperator = '>';
                    delete this.searchForm.maxStatus;
                    delete this.searchForm.maxStatusOperator;
                    delete this.searchForm.docStatus;
                }
            },
            //
            handleRefresh() {
                this.getData();
                this.detailForm = {};
                this.detailId = null;
                this.tabDataI = [];
            },
            getInitPage() {
                this.pageObj.commonPage = 1;
                this.pageObj.commonSize = 10;
                this.getData();
            },
            //
            handleReset() {
                this.searchForm = {};
                this.handleRefresh();
            },
            //
            changeRefresh() {
                this.getData(false);
                this.otherVariable.isChildEdit = false;
            },
            //
            changeImport(val) {
                if(val) {
                    this.searchForm.docStatus = 100
                }else {
                    delete this.searchForm.docStatus;
                    delete this.searchForm.tempStatus;
                }
                this.getData();
            },
            //
            handleDetail(row) {
                row.docStatus === 100 ? this.isBanOperate = false : this.isBanOperate = true;
                this.tabDataI = [];
                this.copyDataI = [];
                this.isShowNotAllowed = true;
                this.loadingI = true;
                this.isShowStatus.isRead = true;
                this.detailForm = {};
                this.fetch('/oms/sls/SalesOrderMaster/getClientOrderMasterForSignAudit',{fid:row.fid})
                    .then(result => {
                        this.isShowNotAllowed = false;
                        if(result.status === 200) {
                            this.detailForm = result.data.list[0];
                            this.copyForm = this.$lodash.cloneDeep(result.data.list[0]);
                        }else {
                            this.$message({
                                message: '',
                                type: 'warning'
                            });
                            this.detailForm = {};
                            this.copyForm = {};
                        }
                    }).then(()=>{
                    this.loadingI = false;
                });
                this.getAjax(`/oms/sls/SalesOrderDetail/getClientOrderDetail?pageSize=1000&clientOrderMasterId=${row.fid}`)
                    .then(result => {
                        if(result.data && result.data.list) {
                            this.tabDataI = result.data.list;
                            this.copyDataI = this.$lodash.cloneDeep(this.tabDataI);
                        }
                    })
                    .then(() => {
                        this.loadingI = false;
                        this.$refs['childTable'].initStatus();
                    });
                this.detailId = row.fid;
            },
            handleSelectionChange(rows) {
                this.selectOrderList = [];
                rows.forEach(item => {
                    this.selectOrderList.push(item.fid)
                })
            },
            //
            handleMulImport() {
                if(this.selectOrderList.length) {
                    this.$confirm(''+this.selectOrderList.length+'?',  '', {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        type: 'warning'
                    }).then(() => {
                        let resultStatus;
                        this.otherVariable.isSaving = true;
                        this.fetch('/oms/sls/SalesOrderMaster/batchSignAuditClientOrder',this.selectOrderList)
                            .then(result => {
                                resultStatus = result.status;
                                if(result.status ===200){
                                    this.$message({
                                        message:'',
                                        type:'success'
                                    });
                                    this.detailForm = {};
                                    this.copyForm = {};
                                    this.tabDataI = [];
                                    this.copyDataI = [];
                                }else{
                                    this.$message({
                                        message:this.returnErrorMsg(result.error,'second'),
                                        type:'error'
                                    })
                                }
                            })
                            .then(() => {
                                if(resultStatus === 200) {
                                    this.$refs['childTable'].initStatus();
                                    this.getData();
                                }
                                this.otherVariable.isSaving = false;
                            })

                    })


                }else {
                    this.$message({
                        message: '',
                        type:'warning'
                    })
                }
            },
            //
            handleImport() {
                if(this.detailForm.fid) {
                    this.$confirm('?', '', {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        type: 'warning'
                    }).then(() => {
                        this.otherVariable.isSaving = true;
                        this.getAjax(`/oms/sls/SalesOrderMaster/signAuditOrder?orderId=${this.detailForm.fid}&docStatus=${this.detailForm.docStatus}`)
                            .then(result => {
                                if(result.status ===200){
                                    this.$message({
                                        message:result.message,
                                        type:'success'
                                    });
                                    this.detailForm = {};
                                    this.copyForm = {};
                                    this.tabDataI = [];
                                    this.copyDataI = [];
                                    this.getData(false);
                                }else {
                                    this.$message({
                                        message:this.returnErrorMsg(result.error,'second'),
                                        type:'error'
                                    })
                                }
                            })
                            .then(() => {
                                this.otherVariable.isSaving = false;
                            })
                    })
                }else {
                    this.$message({
                        message: '',
                        type: 'warning'
                    })
                }
            },
            handleUnImport() {
                if(this.detailForm.fid) {
                    this.getAjax(`/oms/sls/SalesOrderMaster/unSignAuditOrder?orderId=${this.detailForm.fid}&docStatus=${this.detailForm.docStatus}`)
                        .then(result => {
                            this.otherVariable.isSaving = true;
                            if(result.status ===200){
                                this.$message({
                                    message:result.message,
                                    type:'success'
                                });
                                this.detailForm = {};
                                this.copyForm = {};
                                this.tabDataI = [];
                                this.copyDataI = [];
                                this.getData(false);
                            }else {
                                this.$message({
                                    message:this.returnErrorMsg(result.error,'second'),
                                    type:'error'
                                })
                            }
                        })
                        .then(() => {
                            this.otherVariable.isSaving = false;
                        })
                }else {
                    this.$message({
                        message: '',
                        type: 'warning'
                    })
                }
            }
        }
    }
</script>

<style lang="less" scoped>
    .popup-enter-active, .popup-leave-active {
        transition: left 0.1s;
        left: 0;
    }
    .popup-enter, .popup-leave-to {
        left: 100%;
    }
</style>



